<template>
    <div>
        <nav-bar :name="title"></nav-bar>

        <v-card class="ma-1" cols="12">
        <v-container>
        <v-row>
            <v-col cols="12">
            <v-text-field
            class="mb-n5"
            outlined
            dense
            required
            v-model= "store.name"
            label= "*Name"
            ></v-text-field>
            </v-col>
            <v-col cols="12">
            <v-text-field
            class="mb-n5"
            outlined
            dense
            v-model= "store.phone_number"
            label= "Phone number"
            ></v-text-field>
            </v-col>
            <v-col cols="12">
            <v-text-field
            class="mb-n5"
            outlined
            dense
            v-model= "store.email"
            label= "*Email"
            ></v-text-field>
            </v-col>
            <v-col cols="12">
            <v-text-field
            class="mb-n5"
            outlined
            dense
            v-model= "store.address"
            label= "Address"
            ></v-text-field>
            </v-col>
            <v-col cols="12">
            <v-text-field
            class="mb-n5"
            outlined
            dense
            type="number"
            v-model= "store.zipcode"
            label= "Zip code"
            ></v-text-field>
            </v-col>
            <v-col cols="12">
            <v-text-field
            class="mb-n5"
            outlined
            dense
            v-model= "store.state"
            label= "State"
            ></v-text-field>
            </v-col>
            <v-col cols="12">
            <v-select
            class="mb-n5"
            :items="items"
            label="Status"
            outlined
            dense
            v-model= "store.status"
            ></v-select>
            </v-col>
            <v-col>
            <router-link to="/stores">
            <v-btn
            class="mx-4 my-2"
            color="#989898"
            dark
            >
            GO BACK
            </v-btn>
            </router-link>
            <v-btn
            class="mx-4 my-2"
            color="#E47911"
            dark
            :loading="loading"
            @click= "add_store_admin"
            >
            ADD STORE
            </v-btn>
            </v-col>
        </v-row>
        </v-container>
        </v-card>
    </div>
</template>

<script>
    import NavBar from '@/components/NavBar';
    import StoreService from '@/services/StoreService';
    import Swal from 'sweetalert2'

    export default {
        components: {
        NavBar
        },

        data: () => ({
            title: 'ADD STORE',
            loading: false,
            store: {
                name: "",
                email: "",
                phone_number: "",
                address: "",
                zipcode: null,
                state: "",
                status: "",
            },
            items: [
            { text: 'Active', value: "A" },
            { text: 'Pending activation', value: "PA" },
            { text: 'Suspended', value: "S" },
            { text: 'Denied activation', value: "DA" },
            { text: 'Vacation', value: "V" },
            { text: 'Everyday', value: "ED" },
            ]
        }),

        methods:{
            add_store_admin(){
                this.loading = true
                StoreService
                    .add_store_admin(this.store)
                    .then(res => {
                        Swal.fire({
                        icon: 'success',
                        title: 'Success',
                        text: 'The store was successfully added.'
                        }).then(() => {
                            this.loading = false
                            this.$router.push('/stores')
                        })
                    })
                    .catch(e => {
                        /* 
                        *  SI HAY UN ERROR LO CAPTURA Y LO MUESTRA EN UNA MODAL
                        */
                    console.log(e.response.data)
            
                        Swal.fire({
                        icon: 'error',
                        title: 'Error',
                        text: e.response.data['non_field_errors']
                        });
            
                    })
            }

        },

    }
</script>